    // domainApi: this.$store.state.domainApi,
  }
  rules: {} = {
    // domainApi: {
    //   required: true, 
    //   trigger: 'blur', 
    //   message: '请输入'
    // },
  }

  /* lifecycle hook */
  created(){

  }

  /* method */
  submitForm(formName: string) { // 表单提交校验
    (this.$refs[formName] as any).validate((valid: boolean) => {
      if (valid) {
        this.visible = false
      }
    })
  }
}
